import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Sensibilisation } from 'src/app/models/sensibilisation.model';
import { FormBuilder } from '@angular/forms';
import { SensibilisationService } from 'src/app/services/sensibilisation.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-sensibilisation',
  templateUrl: './sensibilisation.component.html',
  styleUrls: ['./sensibilisation.component.scss']
})
export class SensibilisationComponent implements OnInit {

      //declaration
  sensibilisations: Observable<Sensibilisation[]>;
  data: any;
  sensibilisation: Sensibilisation = new Sensibilisation();
  submitted = false;
  id: number;
  constructor( private _formBuilder: FormBuilder,
    private sensibilisationService: SensibilisationService,
    private router:Router,
    private route: ActivatedRoute) { }

  ngOnInit() {
    //pour la liste 
    this.reloadData();
  }

   reloadData() {
    this.sensibilisationService.getSensibilisationsList().subscribe(
      data =>{
      this.data = data;
      for(let f of this.data){
        f.sensibilisation.datePublication=f.sensibilisation.datePublication.substr(0,10);
      }
    //alert(this.data.chemin);
    },error=>{
      console.log(error);
    });
    }
     
    
    
  deleteSensibilisation(id: number) {
    this.sensibilisationService.deleteSensibilisation(id)
    .subscribe(
    data => {
    console.log(data);
    this.reloadData();
    },
    error => console.log(error));
    alert("supprimer avec succes");
    }
     
    
    /* updateSensibilisation(id: number){
    this.router.navigate(['update', id]);
    } */
    
}
