import { Component, OnInit } from '@angular/core';
import { DepistageService } from 'src/app/services/depistage.service';
import { Router } from '@angular/router';
import { AuthentificationService } from 'src/app/services/authentification.service';

@Component({
  selector: 'app-list-depistage',
  templateUrl: './list-depistage.component.html',
  styleUrls: ['./list-depistage.component.scss']
})
export class ListDepistageComponent implements OnInit {
  depistages:any[];
  constructor(private depistageService:DepistageService,private authService: AuthentificationService){

  }

  ngOnInit() { 
    this.GetListDepistage();
    document.getElementById('depistage').className="active"
  }

  GetListDepistage(){
    this.depistageService.GetDepistage().subscribe(
      reponse=>{
        this.depistages=reponse;
      },error=>{
        console.log(error);
      }
    );
  }

  SupprimerDepistage(element){
    this.depistageService.DeleteDepistage(element.depistage.id).subscribe(
      reponse=>{
        console.log(JSON.stringify(element))
        this.GetListDepistage();
      },error=>{
        console.log ("erreur de suppression de l'element ");
      }
    )
  }
}
