import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Utilisateur } from 'src/app/models/Utilisateur.model';
import { AuthentificationService } from 'src/app/services/authentification.service';
import { HttpClient } from '@angular/common/http';
import { JwtHelperService } from "@auth0/angular-jwt";
import { Router } from '@angular/router';
import { Users } from 'src/app/models/users.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  private roles:Array<any>;
  constructor(private authService:AuthentificationService,private http:HttpClient,private router:Router,) { }

  ngOnInit() {
    localStorage.removeItem('token');
  }

  Authentifier(fo:NgForm){
    const utilisat:Users=new Users();
    utilisat.username=fo.value.username;
    utilisat.password=fo.value.password;
   
      this.authService.LoginUser(utilisat).subscribe(
        response=>{
          let token:string=response.headers.get('Authorization'); 
          localStorage.setItem('token',token);
          const helper = new JwtHelperService();
         location.href='accueil';
        },error=>{
          console.log(error);
        }
      )

  }
}
