import { Component, OnInit } from '@angular/core';
import { Gallery } from 'src/app/models/gallery.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Media } from 'src/app/models/Media.model';
import { GalleryService } from 'src/app/services/gallery.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-galery',
  templateUrl: './galery.component.html',
  styleUrls: ['./galery.component.scss']
})
export class GaleryComponent implements OnInit {
  gallery: Gallery = new Gallery();
  submitted = false;
  generalPanel = true;
  localCreatForm: FormGroup;
  media = new Media();
  selectedFiles: FileList;
  listeFichier:File[]=[];
  constructor( private galleryService: GalleryService,
    private _formBuilder: FormBuilder,
    private router:Router,
    private route: ActivatedRoute ) { 
      this.localCreatForm= this._formBuilder.group({ 
        titre: [null, [Validators.required, Validators.minLength(3)]],
          }); 

    }

  ngOnInit() {
  }


  newGallery(): void {
    this.submitted = false;
    this.gallery = new Gallery();
  }

  save() {
    console.log(this.gallery);
    this.galleryService.saveGallery(this.gallery,this.listeFichier)
      .subscribe(
        data => {
          //location.href='administration/list-gallery';
          this.router.navigate(['administration/list-gallery']);

          console.log(data);
        }, error =>{
          console.log(error);
        }
         );
  }

  onSubmit() {
    this.submitted = true;
    this.save();
  }


  selectFile(event) {
    this.selectedFiles = event.target.files;
    let nom = event.target.files.item(0).name;
    for(let i=0;i<this.selectedFiles.length;i++){
      this.listeFichier.push(this.selectedFiles.item(i));
    }
  }
}
