import { Component, OnInit } from '@angular/core';
import { UtilisateursService } from 'src/app/services/utilisateurs.service';
import { Utilisateur } from 'src/app/models/Utilisateur.model';
import { NgForm } from '@angular/forms';
import { AuthentificationService } from 'src/app/services/authentification.service';

@Component({
  selector: 'app-liste-utilisateur',
  templateUrl: './liste-utilisateur.component.html',
  styleUrls: ['./liste-utilisateur.component.scss']
})
export class ListeUtilisateurComponent implements OnInit {

  utilisateurs:Utilisateur[];
  user:Utilisateur=new Utilisateur();
  userMod:Utilisateur=new Utilisateur();;
  erreurPassword:string;
  suppreme:string;
  constructor(private utilisateurService:UtilisateursService,private authService:AuthentificationService) {
    if(!this.authService.Access()){
      location.href="login";
    }

    if(this.authService.GetRoleUtilisateur()=="ADMINSUPPREME"){
      this.suppreme=this.authService.GetRoleUtilisateur();
    }else{

    }
  }

  ngOnInit() {
    
    this.GetListUtilisateur();
  }

  AffecteElement(utilisateur:Utilisateur){
    this.user=utilisateur;
    console.log(this.user);
  }

  AffecteElementEdit(utilisateur:Utilisateur){
    this.userMod=utilisateur;
  }
  GetListUtilisateur(){
    this.utilisateurService.GetListUser().subscribe(
      reponse=>{
        this.utilisateurs=reponse;
      },error=>{
        console.log(error);
      }
    )
  }
  
  EnregisterUtilisateur(fo:NgForm){
    document.getElementById("closeEdit").click();
    const user:Utilisateur=new Utilisateur();
    user.nom=fo.value.nom;
    user.prenom=fo.value.prenom;
    user.login=fo.value.login;
    user.profil=fo.value.profil;
    user.password=this.userMod.password;
    this.utilisateurService.SaveUtilisateur(user).subscribe(
      reponse=>{
        if(reponse!=null){
          location.href='administration/list-utilisateur';
        }else{
          console.log("le login existe deja")
        }
      },error=>{
        console.log(" erreur d'enregistrement ");
      }
    )
  }

  SupprimerUtilisateur(){
    document.getElementById("closeDel").click();
    this.utilisateurService.DeleteUtilisateur(this.user.id).subscribe(
      reponse=>{
        this.GetListUtilisateur();
      },error=>{
        console.log("erreur de suppression de l'utilisateur ");
      }
    );
  }

}
