import { Component, OnInit } from '@angular/core';
import { Soin } from 'src/app/models/soin.model';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Media } from 'src/app/models/Media.model';
import { SoinService } from 'src/app/services/soin.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-soin',
  templateUrl: './soin.component.html',
  styleUrls: ['./soin.component.scss']
})
export class SoinComponent implements OnInit {

  soin: Soin = new Soin();
  submitted = false;
  generalPanel = true;
  localCreatForm: FormGroup;
  media = new Media();
  selectedFiles: FileList;
  listeFichier:File[]=[];
  constructor( private soinService: SoinService,
    private _formBuilder: FormBuilder,
    private router:Router,
    private route: ActivatedRoute) { }

  ngOnInit() {
    this.localCreatForm= this._formBuilder.group({ 
      titre: [null, [Validators.required, Validators.minLength(3)]],
      description: [null, [Validators.required, Validators.minLength(3)]],
        }); 
  }

  newSensibilisation(): void {
    this.submitted = false;
    this.soin = new Soin();
  }

  save() {
    console.log(this.soin);
    this.soinService.saveSoin(this.soin,this.listeFichier)
      .subscribe(
        data =>{
          //location.href='administration/list-soin';
          this.router.navigate(['administration/list-soin']);

          console.log(data)
        }, error =>{
          console.log(error);
        });
  }

  onSubmit() {
    this.submitted = true;
    this.save();
  }

  /* OnDateChange(event){ 
    let date:Date=event;
    console.log(date);
  }
 */
 
  selectFile(event) {
    this.selectedFiles = event.target.files;
    let nom = event.target.files.item(0).name;
    for(let i=0;i<this.selectedFiles.length;i++){
      this.listeFichier.push(this.selectedFiles.item(i));
    }
  }

}
