import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Formation } from 'src/app/models/Formation.model';
import { FormationService } from 'src/app/services/formation.service';
import { Router } from '@angular/router';
import { AuthentificationService } from 'src/app/services/authentification.service';

@Component({
  selector: 'app-formation',
  templateUrl: './formation.component.html',
  styleUrls: ['./formation.component.scss']

})
export class FormationComponent implements OnInit {
  selectedFiles: FileList;
  listeFichier:File[]=[];
  constructor(private formationService : FormationService,private authService: AuthentificationService) {
    if(!this.authService.Access()){
      location.href="login";
    }
  }

  ngOnInit() {
    document.getElementById('formation').className="active"

    
  }

  EnregisterFormation(fo:NgForm){
    console.log(fo);
    const formation:Formation=new Formation();
    formation.titre=fo.value.titre;
    formation.description=fo.value.description;
    formation.dateDebut=fo.value.dateDebut;
    formation.dateFin=fo.value.dateFin;
    this.formationService.saveFormation(formation,this.listeFichier).subscribe(
      reponse=>{
        console.log(reponse)
        //location.href='administration/list-formation';
      },error=>{
        console.log(error);
      }
    ); 
  }

  OnDateChange(event){ 
    let date:Date=event;
    console.log(date);
  }

  selectFile(event) {
    this.selectedFiles = event.target.files;
    let nom = event.target.files.item(0).name;
    for(let i=0;i<this.selectedFiles.length;i++){
      this.listeFichier.push(this.selectedFiles.item(i));
    }
  }
}
