import { Component, OnInit } from '@angular/core';
import { FormationService } from 'src/app/services/formation.service';
import { Router } from '@angular/router';
import { Formation } from 'src/app/models/Formation.model';

@Component({
  selector: 'app-list-formation',
  templateUrl: './list-formation.component.html',
  styleUrls: ['./list-formation.component.scss']
})
export class ListFormationComponent implements OnInit {

  formations:any[];
  constructor(private formationService:FormationService,private router: Router) { }

  ngOnInit() {
    this.GetListFormation();
    document.getElementById("formation").className="active";
  }

  GetListFormation(){
    this.formationService.GetFormation().subscribe(
      reponse=>{
        this.formations=reponse;
        for(let f of this.formations){
          f.formation.dateDebut=f.formation.dateDebut.substr(0,10);
          f.formation.dateFin=f.formation.dateFin.substr(0,10);
        }
        console.log(this.formations);
      },error=>{
        console.log(error)

      }
    ) 
    
  }

  SupprimerFormation(element){
    const formation:Formation=element.formation;
    this.formationService.DeleteFormation(element.formation.id).subscribe(
      reponse=>{
        console.log("formation supprimer avec succes ")
        this.GetListFormation();
      },error=>{
        console.log("erreur de suppression de la formation "+JSON.stringify(error))
      }
    )
  }
}
