import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Utilisateur } from '../models/Utilisateur.model';

@Injectable({
  providedIn: 'root'
})
export class UtilisateursService {

  token=localStorage.getItem('token');
  url='api/utilisateurs';

  constructor(private http:HttpClient) { 
  }

  SaveUtilisateur(user:Utilisateur):Observable<any>{
    return this.http.post(this.url+"/save",user,{headers:new HttpHeaders({'Authorization':this.token})});
  }

  GetListUser():Observable<any>{
    return this.http.get(this.url+"/list",{headers:new HttpHeaders({'Authorization':this.token})});
  }

  DeleteUtilisateur(id:number):Observable<any>{
   return  this.http.delete(this.url+"/delete/"+id,{headers:new HttpHeaders({'Authorization':this.token})})
  }
  
  UpdateUtilisateur(user:Utilisateur):Observable<any>{
    return this.http.put(this.url+"/update",user,{headers:new HttpHeaders({'Authorization':this.token})});
  }

  UpdatePassword(user:Utilisateur):Observable<any>{
    return this.http.put(this.url+"/update-passe",user,{headers:new HttpHeaders({'Authorization':this.token})});
  }

}
