import { Injectable } from '@angular/core';

import { Depistage } from '../models/Depistage.model';
import { Observable } from 'rxjs';
import { HttpHeaders, HttpRequest, HttpClient } from '@angular/common/http';


const headers = new HttpHeaders();
headers.set("Access-control-Allow-Origin","*");
headers.append("Content-Type","multipart-form-data")

@Injectable({
  providedIn: 'root'
})
export class DepistageService {

  url='api/depistage';
  token=localStorage.getItem('token');
  constructor(private http:HttpClient) { 
  }

  SaveDepistage(dep:Depistage,file: File[]): Observable<any>{
    const formdata: FormData = new FormData();
     for(let i=0;i<file.length;i++){
      formdata.append('files',file[i]); 
    }
    const dateD=dep.dateDebut+"";
    const dateF=dep.dateFin+"";
    formdata.append('titre',dep.titre);
    formdata.append('description',dep.description);
    formdata.append("dateDebut",dateD);
    formdata.append("dateFin",dateF);
    const req = new HttpRequest('POST', this.url+"/save",formdata,{headers:new HttpHeaders({'Authorization':this.token})});
    req.responseType,'json';
    req.reportProgress,true;
    console.log(req)
    return this.http.request(req);
  }

  GetDepistage():Observable<any>{
    return this.http.get(this.url+"/list");
  } 

  DeleteDepistage(id:number):Observable<any>{
    return this.http.delete(this.url+"/delete/"+id,{headers:new HttpHeaders({'Authorization':this.token})});
  }

}
