import { Component, OnInit } from '@angular/core';
import { Depistage } from 'src/app/models/Depistage.model';
import { NgForm } from '@angular/forms';
import { DepistageService } from 'src/app/services/depistage.service';
import { Router } from '@angular/router';
import { AuthentificationService } from 'src/app/services/authentification.service';

@Component({
  selector: 'app-ajout-depistage',
  templateUrl: './ajout-depistage.component.html',
  styleUrls: ['./ajout-depistage.component.scss']
})
export class AjoutDepistageComponent implements OnInit {

  selectedFiles: FileList;
  listeFichier:File[]=[];
  constructor(private depistageService:DepistageService,private authService:AuthentificationService) { 
    if(!this.authService.Access()){
      location.href="login";
    }
  }

  ngOnInit() {
    document.getElementById("depistage").className="active";
  }

  EnregisterDepistage(fo:NgForm){
    console.log(fo);
    const depistage:Depistage=new Depistage();
    depistage.titre=fo.value.titre;
    depistage.description=fo.value.description;
    depistage.dateDebut=fo.value.dateDebut;
    depistage.dateFin=fo.value.dateFin;
    this.depistageService.SaveDepistage(depistage,this.listeFichier).subscribe(
      reponse=>{
        location.href='administration/list-depistage';
      },error=>{
        console.log(error);
      }
    )
  }

  selectFile(event) {
    this.selectedFiles = event.target.files;
    let nom = event.target.files.item(0).name;
    for(let i=0;i<this.selectedFiles.length;i++){
      this.listeFichier.push(this.selectedFiles.item(i));
    }
  }

}
