import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Formation } from '../models/Formation.model';

const headers = new HttpHeaders();
headers.set("Access-control-Allow-Origin","*");
headers.append("Content-Type","multipart-form-data")

@Injectable({
  providedIn: 'root'
})
export class FormationService {

  token=localStorage.getItem('token');
  url='api/formation';
  constructor( private http:HttpClient) { }

  saveFormation(formation: Formation,file: File[]): Observable<any>{
    const formdata: FormData = new FormData();
     for(let i=0;i<file.length;i++){
      formdata.append('files',file[i]); 
    }
    const dateD=formation.dateDebut+"";
    const dateF=formation.dateFin+"";
    formdata.append('titre',formation.titre);
    formdata.append('description',formation.description);
    formdata.append("dateDebut",dateD);
    formdata.append("dateFin",dateF)
    headers.set("Authorization",this.token);
    const req = new HttpRequest('POST', this.url+"/save",formdata,{headers:new HttpHeaders({'Authorization':this.token})});
    req.responseType,'json';
    req.reportProgress,true;
    console.log(req)
    return this.http.request(req);
  }

  GetFormation():Observable<any>{
    return this.http.get(this.url+"/list");
  }

  DeleteFormation(id:number):Observable<any> {
    return this.http.delete(this.url+"/delete/"+id,{headers:new HttpHeaders({'Authorization':this.token})})
  }
}
