// fonction agui
 
saveSensibilisation(sensibilisation: Sensibilisation,file: File[]): Observable<any>{
  const formdata: FormData = new FormData();
   for(let i=0;i<file.length;i++){
    formdata.append('files',file[i]); 
  }
  const dateD=sensibilisation.datePublication+"";
 
  formdata.append('titre',sensibilisation.titre);
  formdata.append('description',sensibilisation.description);
  formdata.append("datePublication",dateD);
  headers.set("Authorization",this.token);
  const req = new HttpRequest('POST', "api/sensibilisation/save",formdata,{headers:new HttpHeaders({'Authorization':this.token})});
  req.responseType,'json';
  req.reportProgress,true;
  console.log(req);
  return this.http.request(req);
}
