import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { Soin } from 'src/app/models/soin.model';
import { FormBuilder } from '@angular/forms';
import { SoinService } from 'src/app/services/soin.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-list-soin',
  templateUrl: './list-soin.component.html',
  styleUrls: ['./list-soin.component.scss']
})
export class ListSoinComponent implements OnInit {

 //declaration
soins: Observable<Soin[]>;
data: any;
soin: Soin = new Soin();
submitted = false;
id: number;
constructor(private _formBuilder: FormBuilder,
private soinService: SoinService,
private router:Router,
private route: ActivatedRoute ) {
       
}
  ngOnInit() {
    this.reloadData();
  }

  reloadData() {
    //pour la liste 
    this.soinService.getSoinsList().subscribe(data =>{
      this.data = data;
      //alert(this.data.chemin);
      });    }
    
  deleteSoin(id: number) {
    this.soinService.deleteSoin(id).subscribe(
      data => {
      console.log(data);
      this.reloadData();
    },error => 
      console.log(error)
    );
    alert("supprimer avec succes");
  }
}
