import { Component, OnInit } from '@angular/core';
import { DepistageService } from 'src/app/services/depistage.service';

@Component({
  selector: 'app-depistage',
  templateUrl: './depistage.component.html',
  styleUrls: ['./depistage.component.scss']
})
export class DepistageComponent implements OnInit {

  depistages:any[];
  constructor(private depistageService:DepistageService) { }

  ngOnInit() {
    this.GetListDepistage();
    document.getElementById('dep').className='active';

  }

  GetListDepistage(){
    this.depistageService.GetDepistage().subscribe(
      reponse=>{
        this.depistages=reponse;
        for(let elt of this.depistages){
          elt.depistage.dateDebut=elt.depistage.dateDebut.substr(0,10);
          elt.depistage.dateFin=elt.depistage.dateFin.substr(0,10);
        }
      },error=>{
        console.log(error);
      }
    );
  }

}
