import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Soin } from '../models/soin.model';

const headers = new HttpHeaders();
headers.set("Access-control-Allow-Origin","*");
headers.append("Content-Type","multipart-form-data")
@Injectable({
  providedIn: 'root'
})
export class SoinService {
  token=localStorage.getItem('token');
  constructor(private http: HttpClient) { }

  getSoin(id: number): Observable<Object>{
    let headers = new HttpHeaders();
    headers.append('Content-type','application/json');
    return this.http.get('api/soins/'+id,{headers:headers});
  }
// fonction agui
 
saveSoin(soin: Soin,file: File[]): Observable<any>{
  const formdata: FormData = new FormData();
   for(let i=0;i<file.length;i++){
    formdata.append('files',file[i]); 
  }
  formdata.append('titre',soin.titre);
  formdata.append('description',soin.description);
  headers.set("Authorization",this.token);
  const req = new HttpRequest('POST', "api/soin/save",formdata,{headers:new HttpHeaders({'Authorization':this.token})});
  req.responseType,'json';
  req.reportProgress,true;
  console.log(req);
  return this.http.request(req);
}
//fin fonction agui
 
 
  updateSoin(id: number, value: any): Observable<Object> {
    let headers = new Headers();
    return this.http.put('api/soins/'+id,value);
     }
 

       deleteSoin(id: number){
        return this.http.delete('api/soin/delete/'+id,{headers:new HttpHeaders({'Authorization':this.token})});
       }
      
  getSoinsList(): Observable<any> {
    return this.http.get('api/soin/list');
  }
  
}
